.login{
    background-image: url("../images/back1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
  }
  
  .login__container{
    display: grid;
    place-items: center;
    height: 80%;
    width: 50%;
    margin: auto;
    box-shadow: 0px 8px 20px -5px black;
    background-image: url("../images/back.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }

  .login__container>h1{
    font-family: "Recursive",serif;
  }

  .login__input > .address > input,
  .login__input > .name> input{
    display: flex;
    flex-direction: column;
    border: none;
    border-bottom: 2px solid black;
    padding: 10px;
    margin-bottom: 20px;
    width: 80%;
    background-color: transparent;
  }

  .address,.name{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 30px;
  }

  p{
    font-size: 12px;
    font-weight: bold;
  }